.log-in-container {
  height: auto;
  width: 80rem;
  border-radius: 8%;
  text-align: center;
  color: rgb(0, 0, 0);
  background-color: #ffffff;
  margin: 15rem 5rem 5rem 5rem;
  border: 0.3rem solid #ffb300;
  box-shadow: 0 0 0.5rem #0000003d;
}

.container-header {
  margin: 2rem 2rem 7rem 2rem;
  font-size: 3rem;
  font-weight: 700;
}

.grid label {
  display: block;
  font-size: 1.6rem;
  font-weight: 500;
}

.grid input {
  margin-left: 25%;
  font-family: inherit;
  width: 50%;
  padding: 1.2rem;
  border-radius: 0.9rem;
  border: 0.1rem solid #00000080;
  font-size: 1.3rem;
}

.sign-up-bg {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 96rem;
  margin-top: 15rem;
  background-color: #40c057;
}

.sign-up-hyperlink:link,
.sign-up-hyperlink:visited {
  color: #5d27da;
  font-size: 1.2rem;
  text-decoration: none;
  border-bottom: 0.1rem solid #5d27da;
  margin: 0 0.5rem 0.5rem 0.5rem;
}
.sign-up-hyperlink:hover,
.sign-up-hyperlink:active {
  color: #df11ce;
  border-bottom: 0.1rem solid #df11ce;
}

.sign-up-hyperlink-container {
  display: flex;
}

.sign-up-hyperlink-container p {
  font-weight: 500;
  color: #000;
  font-size: 1.2rem;
}
